/* Skills 아이콘 마우스오버효과 */


.skills-img>.skills-ico {
  justify-content: space-between;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 200px;
  position: relative;
  overflow:hidden;
  margin-bottom:10px;
}

.skills-img>.skills-ico::after {
  border-radius: 5px;
  content:"";
  background-color:rgba(0,0,0,0);
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  transition:background-color 0.5s;
}


.skills-img>.skills-ico:before {
  font-weight:bold;
  position:absolute;
  top:50%;
  left:50%;
  color:white;
  -webkit-transform:translateX(-50%) translateY(-50%);
          transform:translateX(-50%) translateY(-50%);
  padding:10px;
  border-radius:10px;
  z-index:1;
  opacity:0;
  transition:opacity 0.5s;
}
.s-html:nth-child(1)::before{ 
  content:"HTML";
}
.s-css:nth-child(2)::before{ 
  content:"CSS";
}
.s-js:nth-child(3)::before{ 
  content:"JavaScript";
}
.s-react:nth-child(4)::before{ 
  content:"React";
}

.s-ts:nth-child(1)::before{ 
  content:"TypeScript";
}
.s-py:nth-child(2)::before{ 
  content:"Python";
}
.s-java:nth-child(3)::before{ 
  content:"Java";
}
 .skills-ico:hover::after {
  background-color:rgba(0,0,0,.5);
}

.skills-img > .skills-ico:hover::before {
  opacity:1;
}

.skills-img > .skills-ico > img {
  transition:-webkit-transform 0.5s;
  transition:transform 0.5s;
  transition:transform 0.5s, -webkit-transform 0.5s;
}
.skills-img > .skills-ico:hover > img {
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
}
